
.field{
    position: relative;
    margin-bottom: 2.2rem;
}

label, input[type=text]{
    display: block;
    user-select: none;
}

input, textarea{
    height: 40px;
    padding: .6rem 1.2rem;
    width: 400px;
    background-color: white;
    border: 1px solid #D8C3A5;
    outline: none;
    transition: all .1s ease-in-out;

    &:focus{
        border: 1px solid#B4B1A7;
    }
}


.text-label{
    z-index: 1;
    position: absolute;
    top: .3rem;
    left: 1rem;
    font-size: 1.2em;
    cursor: text;
    color: rgba(0, 0, 0, 0.68);
}

input[type=checkbox]{
    display: none;
}

.check-forgot{
    display: flex;
    justify-content: space-between;
    align-items: center;

    a{
        color: white;
    }
}

.check{
    label{
        cursor: pointer;
    }
    
    label::before{
        content: '';
        background-color: #D8C3A5;
        display: inline-block;
        width: 1.25rem;
        height: 1.25rem;
        flex-shrink: 0;
        flex-grow: 0;
        border: 1px solid #adb5bd;
        border-radius: .25rem;
        margin-right: .5rem;
        margin-bottom: -.15rem;
        background-repeat: no-repeat;
        background-position: center center;
        background-size: 50% 50%;
    }
    
    input[type=checkbox]:checked+label::before{
        border-color: #D8C3A5;
        background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='red' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e");  
    }
}

.button-pending{
    display: flex;
    position: relative;
    gap: 2rem;
    width: 100%;
}

.ring-wrapper{
    position: absolute;
    right: -1rem;
}

.lds-ring {
    display: inline-block;
    position: relative;
}
    
.lds-ring div {
    box-sizing: border-box;
    display: block;
    position: absolute;
    width: 32px;
    height: 32px;
    border: 6px solid #fff;
    border-radius: 50%;
    animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    border-color: #E85A4F transparent transparent transparent;
}

.lds-ring div:nth-child(1) {
    animation-delay: -0.45s;
}

.lds-ring div:nth-child(2) {
    animation-delay: -0.3s;
}

.lds-ring div:nth-child(3) {
    animation-delay: -0.15s;
}

@keyframes lds-ring {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
